@use '../inc/vars' as *;

.hero {
  color: $text-light;
  &:first-of-type {
    min-height: 100vh;
  }
  .container {
    display: flex;
  }
  .section {
    &__header {
      position: relative;
      margin: auto;
    }
    &__header-main {
      display: flex;
      flex-direction: column;
      gap: 55px;
      .section__title {
        // color: $text;
      }
    }
    &__title {
      color: $text-light;
      
    }
    &__header-additional {
      &:before {
        background-color: $text-light;
      }
    }
    &__subtitle {

    }
    &__body {
      
    }
  }
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    &::after {
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      position: absolute;
      content: '';
      background-color: rgba(0, 0, 0, 0.25);
    }
  }
  &__bg-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &--regular {
    align-items: initial;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      flex-direction: column;
      padding-top: 0;
    }
    @media(max-width: $sm){
    
    }
    @media(max-width: $xs){
    
    }
    
    .container {
      flex-direction: column;
    }
    .section__header {
      max-width: 1000px;
      & > * {
        &:nth-of-type(1) {
          max-width: 47%;
          @media(max-width: $xl){
          
          }
          @media(max-width: $lg){
          
          }
          @media(max-width: $md){
            
          }
          @media(max-width: $sm){
            max-width: 100%;
          }
          @media(max-width: $xs){
          
          }
          
        }
        &.section__header-additional {
          max-width: 55%;
          color: $text;
          @media(max-width: $xl){
          
          }
          @media(max-width: $lg){
          
          }
          @media(max-width: $md){
            
          }
          @media(max-width: $sm){
            max-width: 100%;
          }
          @media(max-width: $xs){
          
          }
          &::before {
            background-color: $text;
          }
        }
      }
      &--top {
        margin-left: 0;
        margin-top: 0;
        max-width: 100%;
        margin-bottom: 60px;
        .section__header-additional {
          max-width: 100%;
          min-height: 15vh;
          padding-top: 20px;
        }
      }
    }
    .section__title {
      color: $text;
    }
  }
  &--404 {
    .section__header {
      flex-direction: row;
      // position: static;
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
      
      }
      @media(max-width: $md){
        
      }
      @media(max-width: $sm){
        padding-bottom: 200px;
        height: 100%;
      }
      @media(max-width: $xs){
      
      }
      
      &-additional {
        border-left: 1px solid;
        position: static;
        &::before {
          display: none;
        }
      }
    }
    .section__title {
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
      
      }
      @media(max-width: $md){
        font-size: 60px;
      }
      @media(max-width: $sm){
      
      }
      @media(max-width: $xs){
      
      }
      
    }
    .h2 {
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
      
      }
      @media(max-width: $md){
        font-size: 28px;
      }
      @media(max-width: $sm){
      
      }
      @media(max-width: $xs){
      
      }
      
    }
    .button-container {
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
      
      }
      @media(max-width: $md){
        
      }
      @media(max-width: $sm){
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        flex-direction: column;
        & > * {
          width: 100%;
        }
      }
      @media(max-width: $xs){
      
      }
      
    }
  }
}